<template>
  <div class="wrapper" ref="wrapper" @click="clickGround">
    <div
      class="cross-shadow"
      v-if="moving"
      :style="`transform: translate(${tempChess.x}px,${tempChess.y}px)`"
    >
      <!-- 十字星 -->
    </div>
    <div v-for="item in chessmans" style="position:relative" :key="item.id">
      <div
        class="piece"
        :class="
          `${chessTypes[item.type]} ${
            item.active ? 'active' : ''
          } ${item.status||''}`
        "
        :style="`transform: translate(${item.x}px,${item.y}px)`"
      ></div>
      <div v-if="item.active" class="piece scaleAni"
        :style="`top:${item.y}px;left:${item.x}px`"
      ></div>
    </div>
    <div
      class="piece moving"
      v-if="tempChess"
      :class="chessTypes[tempChess.type]"
      :style="`transform: translate(${tempChess.x}px,${tempChess.y}px)`"
    >
      <!-- “落子”按钮 -->
      <div
        v-if="tempChess && !moving"
        class="confirmBtn1"
        @click.stop="clickConfirmBtn"
      ></div>
    </div>
  </div>
</template>

<script>
const screenWidth =
  document.documentElement.scrollWidth || document.body.scrollWidth
const COLUMNS = 14 // 棋盘列数
const MARGIN = (COLUMNS * screenWidth) / 750 // 棋盘外边界
const PADDING = (28 * screenWidth) / 750 // 棋盘内边界
const OUTWIDTH = MARGIN + PADDING // 棋盘非活动区域的总宽度
const RECTWIDTH = (50 * screenWidth) / 750 // 棋格的宽度

const chessBoard = [] // 模拟棋盘落子的二维数组
const wins = [] // 赢法统计数组
let count = 0 // 赢法的总数目
// 两方角色赢法统计数组
const myWin = []
const AIWin = []
// 角色常量
const Me = 'Me'
const Competitor = 'Competitor'
const chessTypes = { Competitor: 'white', Me: 'black' } // 棋子颜色
// AI思考时间（模拟）
// const THINKTIME = 2000
export default {
  name: 'gobang',
  data () {
    return {
      chessmans: [], // 存放棋盘上已有的棋子 的信息
      tempChess: null, // 玩家移动选点时的棋子
      moving: false,
      chessTypes, // 棋子颜色
      Me, // 我的角色
      Competitor, // 对手的角色
      players: { Me, Competitor },
      competitors: { Me: Competitor, Competitor: Me },
      playing: Me, // 当前执棋人，结束时为null
      winner: null
    }
  },
  watch: {
    tempChess (newval) {
      this.$emit('onAction', newval !== null)
    }
  },
  created () {
    this.init()
  },
  mounted () {
    this.getWrapperInfo()
    this.initTouchEvent()
  },
  methods: {
    // 初始化棋盘和数组
    init () {
      this.initWinType()
      this.cleanChessboard()
    },
    // 初始化赢法统计数组，只需要执行一次，所以init方法不需要再调
    initWinType () {
      for (let i = 0; i < COLUMNS; i++) {
        wins[i] = []
        for (let j = 0; j < COLUMNS; j++) {
          wins[i][j] = []
        }
      }
      // 阳线纵向90°的赢法
      for (let i = 0; i < COLUMNS; i++) {
        for (let j = 0; j < COLUMNS - 4; j++) {
          for (let k = 0; k < 5; k++) {
            wins[i][j + k][count] = true
          }
          count++
        }
      }

      // 阳线横向0°的赢法
      for (let i = 0; i < COLUMNS; i++) {
        for (let j = 0; j < COLUMNS - 4; j++) {
          for (let k = 0; k < 5; k++) {
            wins[j + k][i][count] = true
          }
          count++
        }
      }

      // 阴线斜向135°的赢法
      for (let i = 0; i < COLUMNS - 4; i++) {
        for (let j = 0; j < COLUMNS - 4; j++) {
          for (let k = 0; k < 5; k++) {
            wins[i + k][j + k][count] = true
          }
          count++
        }
      }

      // 阴线斜向45°的赢法
      for (let i = 0; i < COLUMNS - 4; i++) {
        for (let j = COLUMNS - 1; j > 3; j--) {
          for (let k = 0; k < 5; k++) {
            wins[i + k][j - k][count] = true
          }
          count++
        }
      }
    },
    // 回归初始状态,并提供一个初始玩家
    cleanChessboard (player = Me) {
      this.chessmans = []
      this.playing = player
      this.tempChess = null
      this.winner = null
      // 初始化棋盘
      for (let i = 0; i < COLUMNS; i++) {
        chessBoard[i] = []
        for (let j = 0; j < COLUMNS; j++) {
          chessBoard[i][j] = 0
        }
      }
      // 初始化双方赢法的统计数组
      for (let i = 0; i < count; i++) {
        myWin[i] = 0
        AIWin[i] = 0
      }
    },
    getWrapperInfo () {
      const dom = this.$refs.wrapper
      const { left, top } = dom.getBoundingClientRect()
      dom.wrapperX = left
      dom.wrapperY = top
      return dom
    },
    initTouchEvent () {
      let startX = 0
      let startY = 0
      let endX = 0
      let endY = 0
      let tmp = {}
      const dom = this.$refs.wrapper
      const { wrapperX, wrapperY } = dom
      // touchStart事件
      dom.addEventListener(
        'touchstart',
        event => {
          if (this.playing !== 'Me') {
            return
          }
          const touch = event.touches[0] || event
          startX = touch.pageX
          startY = touch.pageY
          if (!this.tempChess) {
            this.clickGround({ // 触摸的时候生成临时棋子（之前是单独的点击事件）
              x: startX,
              y: startY
            })
          }
          tmp = this.tempChess
        },
        false
      )
      // touchmove事件
      dom.addEventListener(
        'touchmove',
        event => {
          if (this.playing !== 'Me') {
            return
          }
          if (!this.tempChess) {
            return
          }
          const touch = event.touches[0] || event
          endX = touch.pageX
          endY = touch.pageY
          const offsetX = endX - startX // 基于touch事件初始位置的偏移量
          const offsetY = endY - startY
          const x = wrapperX + tmp.x + offsetX // 棋子初始位置加上偏移量获得的全局x，y坐标（棋子对象的x，y是基于父容器的，所以要加wrapperXY）
          const y = wrapperY + tmp.y + offsetY
          const { indexX, indexY } = this.transformXY(x, y) // 转换坐标获得棋子位置的序号
          // console.log({ indexX, indexY })
          this.tempChess = this.getChess(indexX, indexY, 'Me') // 更新临时棋子
          this.moving = true
        },
        false
      )
      // touchend事件
      dom.addEventListener(
        'touchend',
        () => {
          if (this.playing !== 'Me') {
            return
          }
          if (!this.tempChess) {
            return
          }
          this.moving = false
          const { indexX, indexY } = this.tempChess
          // 目的地有其他棋子，则回到touch最初状态
          if (chessBoard[indexX][indexY]) {
            this.tempChess = tmp
          }
        },
        false
      )
    },
    // 确认落子
    clickConfirmBtn () {
      if (!this.tempChess) {
        return
      }
      const { indexX, indexY } = this.tempChess
      this.drop(indexX, indexY, 'Me')
      // 检测是否赢，没赢换手
      this.handleDrop(indexX, indexY, 'Me')
    },
    // 点击棋板生成临时棋子
    clickGround (e) {
      if (this.playing !== 'Me') {
        return
      }
      // 点击的坐标
      const { x, y } = e
      const { indexX, indexY } = this.transformXY(x, y)
      // console.log(indexX)
      if (!chessBoard[indexX][indexY]) {
        this.tempChess = this.getChess(indexX, indexY, 'Me')
      }
    },
    /**
     * 将基于页面（不是容器）xy坐标直接转换为最近一个棋子的indexX,indexY编号
     * @param eventX
     * @param eventY
     * */
    transformXY (eventX, eventY) {
      // 棋盘图片左上角在页面上的x、y值
      const {
        left: wrapperX,
        top: wrapperY
      } = this.$refs.wrapper.getBoundingClientRect()
      const minXY = 0 // 【限定】在棋盘上的最小x、y值
      const maxXY = RECTWIDTH * (COLUMNS - 1) // 【限定】在棋盘上的最大x、y值
      const originX = wrapperX + OUTWIDTH // 棋盘的原点坐标x
      const originY = wrapperY + OUTWIDTH // 棋盘的原点坐标y
      let realX = eventX - originX // 棋子基于棋盘原点的坐标x
      let realY = eventY - originY // 棋子基于棋盘原点的坐标y
      realX = realX < minXY ? minXY : realX > maxXY ? maxXY : realX // 限制最大值最小值防止超出棋盘
      realY = realY < minXY ? minXY : realY > maxXY ? maxXY : realY // 同上
      const indexX = Math.round(realX / RECTWIDTH)
      const indexY = Math.round(realY / RECTWIDTH)
      return { indexX, indexY }
    },
    /**
     * 根据棋子编号转换棋子的实际坐标点xy, 返回一个棋子对象
     * @param eventX
     * @param eventY
     * @param type = ['Me', 'Competitor']
     * */
    getChess (indexX, indexY, type = 0) {
      const x = OUTWIDTH + indexX * (RECTWIDTH + 0.5)
      const y = OUTWIDTH + indexY * (RECTWIDTH + 0.5)
      return {
        indexX,
        indexY,
        x,
        y,
        status: '',
        tmpx: x,
        tmpy: y,
        type,
        index: this.chessmans.length
      }
    },
    /**
     * 渲染棋子对象
     * @param chess 棋子对象
     * */
    renderChess (chess) {
      const { indexX, indexY } = chess
      this.chessmans.push(chess)
      chessBoard[indexX][indexY] = chess
      this.refreshActive()
      return [indexX, indexY]
    },
    /**
     * 渲染棋子（对外暴露）
     * @param indexX 棋子基本参数
     * @param indexY 棋子基本参数
     * @param type 棋子基本参数
     * */
    drawChess (indexX, indexY, type) {
      this.renderChess(this.getChess(indexX, indexY, type))
    },
    // 下棋落子，同时并上报给父组件
    drop (indexX, indexY, type = Competitor) {
      this.drawChess(indexX, indexY, type)
      // 剔除临时棋子
      this.tempChess = null
      this.$emit('onDrop', this.chessmans.slice(-1)[0], this.chessmans)
      console.log('gobang playing', this.playing)
    },
    // 处理上一步落子后的事务
    handleDrop (indexX, indexY, type = Competitor) {
      // 计算当前用户操作的结果
      const winner = this.checkWin(indexX, indexY, type)
      // 赢
      if (winner) {
        this.endGame(type)
      } else {
        // 没赢，换手
        this.togglePlayer()
      }
    },
    // 设置最后一个棋子的激活状态
    refreshActive () {
      const lastIndex = this.chessmans.length - 1
      const prev = this.chessmans[lastIndex - 1]
      const last = this.chessmans[lastIndex]
      if (prev) {
        prev.active = false
      }
      if (last) {
        last.active = true
      }
    },
    /**
     * 检查是否胜利
     * @param indexX 将要检查的棋子的X序号
     * @param indexX 将要检查的棋子的Y序号
     * @param who 这是谁下的
     */
    checkWin (indexX, indexY, who) {
      const winTypeNow = wins[indexX][indexY]
      const target = who === 'Me' ? myWin : AIWin
      const otherOne = who === 'Me' ? AIWin : myWin
      let winner = false
      // 遍历赢法统计数组
      for (let k = 0; k < count; k++) {
        if (winTypeNow[k]) {
          // 如果存在赢法,则当前玩家此赢法胜算+1(赢法为5胜取胜)
          target[k]++
          // 如果存在赢法,则对手此赢法胜算赋值为6(永远不等于5,永远无法在此处取胜)
          otherOne[k] = 6
          // 目标落子后,此处赢法数组凑够5,玩家取胜
          if (target[k] === 5) {
            console.log(who + ' Win')
            winner = who
            this.getWinList(indexX, indexY)
            return winner
          }
        }
      }
      return winner
    },
    // 高亮赢的棋子
    getWinList (x, y) {
      if (x === undefined || y === undefined) {
        return
      }
      const current = chessBoard[x][y]
      // 横向
      const st1 = []
      for (let i = -1; i > -5; i--) {
        if (
          chessBoard[x + i] &&
          chessBoard[x + i][y] &&
          chessBoard[x + i][y].type === current.type
        ) {
          st1.push(chessBoard[x + i][y])
        } else {
          break
        }
      }
      for (let i = 0; i < 5; i++) {
        if (
          chessBoard[x + i] &&
          chessBoard[x + i][y] &&
          chessBoard[x + i][y].type === current.type
        ) {
          st1.push(chessBoard[x + i][y])
        } else {
          break
        }
      }
      // 竖向
      const st2 = []
      for (let i = -1; i > -5; i--) {
        if (
          chessBoard[x] &&
          chessBoard[x][y + i] &&
          chessBoard[x][y + i].type === current.type
        ) {
          st2.push(chessBoard[x][y + i])
        } else {
          break
        }
      }
      for (let i = 0; i < 5; i++) {
        if (
          chessBoard[x] &&
          chessBoard[x][y + i] &&
          chessBoard[x][y + i].type === current.type
        ) {
          st2.push(chessBoard[x][y + i])
        } else {
          break
        }
      }
      // 斜1 ↘️
      const st3 = []
      for (let i = -1; i > -5; i--) {
        if (
          chessBoard[x + i] &&
          chessBoard[x + i][y + i] &&
          chessBoard[x + i][y + i].type === current.type
        ) {
          st3.push(chessBoard[x + i][y + i])
        } else {
          break
        }
      }
      for (let i = 0; i < 5; i++) {
        if (
          chessBoard[x + i] &&
          chessBoard[x + i][y + i] &&
          chessBoard[x + i][y + i].type === current.type
        ) {
          st3.push(chessBoard[x + i][y + i])
        } else {
          break
        }
      }
      // 斜2 ↙️
      const st4 = []
      for (let i = -1; i > -5; i--) {
        if (
          chessBoard[x - i] &&
          chessBoard[x - i][y + i] &&
          chessBoard[x - i][y + i].type === current.type
        ) {
          st4.push(chessBoard[x - i][y + i])
        } else {
          break
        }
      }
      for (let i = 0; i < 5; i++) {
        if (
          chessBoard[x - i] &&
          chessBoard[x - i][y + i] &&
          chessBoard[x - i][y + i].type === current.type
        ) {
          st4.push(chessBoard[x - i][y + i])
        } else {
          break
        }
      }
      const temp = [st1, st2, st3, st4]
      // console.log(temp)
      temp.forEach(v => {
        if (v.length === 5) {
          v.forEach((chess) => {
            chess.status = 'win'
          })
        }
      })
    },
    // 结束游戏，输出赢者
    endGame (who) {
      this.playing = null
      this.tempChess = null
      this.winner = who
      setTimeout(() => {
        if (!who || who === 'Competitor') {
          alert('你输了～。0')
        } else {
          alert('你赢了，厉害了厉害了！')
        }
        this.$emit('onEnd', who, this.chessmans)
      }, 3600)
    },
    // 切换当前玩家，改变this.playing
    togglePlayer (setPlayer) {
      const playing = this.playing
      if (setPlayer !== undefined) {
        this.playing = setPlayer
      } else if (playing) {
        this.playing = this.competitors[playing]
      }
    },
    // AI下棋
    AIDO () {
      let u = 0 // 电脑预落子的x位置
      let v = 0 // 电脑预落子的y位置
      let myScore = [] // 玩家的分数
      let AIScore = [] // 电脑的分数
      let max = 0 // 最优位置的分数
      // 初始化分数的二维数组
      for (let i = 0; i < COLUMNS; i++) {
        myScore[i] = []
        AIScore[i] = []
        for (let j = 0; j < COLUMNS; j++) {
          myScore[i][j] = 0
          AIScore[i][j] = 0
        }
      }
      // 通过赢法统计数组为两个二维数组分别计分
      for (let i = 0; i < COLUMNS; i++) {
        for (let j = 0; j < COLUMNS; j++) {
          if (chessBoard[i][j] === 0) {
            for (let k = 0; k < count; k++) {
              if (wins[i][j][k]) {
                if (myWin[k] === 1) {
                  myScore[i][j] += 200
                } else if (myWin[k] === 2) {
                  myScore[i][j] += 400
                } else if (myWin[k] === 3) {
                  myScore[i][j] += 2000
                } else if (myWin[k] === 4) {
                  myScore[i][j] += 10000
                }
                if (AIWin[k] === 1) {
                  AIScore[i][j] += 220
                } else if (AIWin[k] === 2) {
                  AIScore[i][j] += 420
                } else if (AIWin[k] === 3) {
                  AIScore[i][j] += 2100
                } else if (AIWin[k] === 4) {
                  AIScore[i][j] += 20000
                }
              }
            }

            // 如果玩家(i,j)处比目前最优的分数大，则落子在(i,j)处
            if (myScore[i][j] > max) {
              max = myScore[i][j]
              u = i
              v = j
            } else if (myScore[i][j] === max) {
              // 如果玩家(i,j)处和目前最优分数一样大，则比较电脑在该位置和预落子的位置的分数
              if (AIScore[i][j] > AIScore[u][v]) {
                u = i
                v = j
              }
            }

            // 如果电脑(i,j)处比目前最优的分数大，则落子在(i,j)处
            if (AIScore[i][j] > max) {
              max = AIScore[i][j]
              u = i
              v = j
            } else if (AIScore[i][j] === max) {
              // 如果电脑(i,j)处和目前最优分数一样大，则比较玩家在该位置和预落子的位置的分数
              if (myScore[i][j] > myScore[u][v]) {
                u = i
                v = j
              }
            }
          }
        }
      }
      // 下棋
      this.drop(u, v, 'Competitor')
      // 检测是否赢，没赢换手
      this.handleDrop(u, v, 'Competitor')
    }
  }
}
</script>

