  z-index: 200;
}
.confirmBtn1 {
  width: 104px;
  height: 90px;
  margin: 0 auto 20px;
  transform: translate(8px, 10px);
  background-image: url(./images/btn-submit1.png);
  background-repeat: no-repeat;
  background-size: cover;
}
.white {
  background: url(./images/white-default.png) no-repeat center center/ 100% 100%;
  &.active {
    background: url(./images/white-active.png) no-repeat center center/ 100%
      100%;
  }
  &.win {
    background: url(./images/white-succeed.png) no-repeat center center/ 100%
      100%;
  }
  &.moving {
    background: url(./images/white-active.png) no-repeat center center/ 66px 66px;
  }
