<template>
  <div class="gobang-page">
    <!-- 用户对战头部 -->
    <div class="users-wrapper">
      <div class="avatar avatar-black">
        <div class="avatar-img"></div>
      </div>
      <span class="user-name user-name-left">🤖️大河3号</span>
      <span class="user-name user-name-right">你🍅</span>
      <div class="avatar avatar-white">
        <div class="avatar-img"></div>
      </div>
    </div>
    <!-- 棋盘 -->
    <Gobang @onDrop="onDrop" @onEnd="onEnd" @onAction='onAction' ref='gobang'/>
    <!-- 倒计时和按钮 -->
    <div class="menu">
      <div
        class="my-turn"
        :style="`visibility: ${playing === 'Me' ? 'visible' : 'hidden'};`"
      >
        <div class="count-shadow"></div>
        <div class="count-wrapper">
          <div class="count-down">{{ countNum }}s</div>
          <div class="tip">我的回合</div>
        </div>
      </div>
      <div class="givein" @click="giveIn"></div>
      <div class="your-turn" v-show="playing !== 'Me'">
        <div class="count-shadow"></div>
        <div class="count-wrapper">
          <div class="count-down">{{ countNum }}s</div>
          <div class="tip">对方回合</div>
        </div>
      </div>
      <div class="confirmBtn" v-show="actioning" @click="clickConfirmBtn"></div>
    </div>
  </div>
</template>
<script>
import Gobang from './gobang'
// 倒计时时间
const COUNT = 15
export default {
  name: 'gobang',
  components: {
    Gobang
  },
  data () {
    return {
      modalType: 1,
      showModal1: true,
      showModal2: true,
      showModal3: true,
      countNum: COUNT, // 倒计时时间
      actioning: false,
      playing: 'Me', // 当前执棋人，结束时为null
      winner: null
    }
  },
  created () {
  },
  mounted () {
  },
  methods: {
    // 每下一步棋后返回
    onDrop (chess, chesses) {
      console.log(chess.type, chesses.length)
      if (chess.type === 'Me') {
        // this.$refs.gobang.drop(1, 2)
        this.playing = 'Competitor'
        this.timer2 = setTimeout(() => {
          this.$refs.gobang.AIDO()
        }, 200)
      } else {
        this.playing = 'Me'
      }
      this.countDown()
    },
    // 棋局结束
    onEnd (winner, chesses) {
      console.log({ winner, chesses })
      this.countNum = COUNT
      clearInterval(this.timer)
      clearTimeout(this.timer2)
      this.$refs.gobang.cleanChessboard()
    },
    onAction (e) {
      this.actioning = e
    },
    // 认输
    giveIn () {
      clearInterval(this.timer)
      clearTimeout(this.timer2)
      this.$refs.gobang.endGame('Competitor')
    },
    // 确认落子
    clickConfirmBtn () {
      this.$refs.gobang.clickConfirmBtn()
    },
    t1 () {
      console.log(1)
    },
    // 执行倒计时、双方使用同一个倒计时参数，靠play隐藏
    countDown () {
      clearInterval(this.timer)
      this.countNum = COUNT
      this.timer = setInterval(() => {
        if (this.countNum > 0) {
          this.countNum -= 1
        } else {
          clearInterval(this.timer)
        }
      }, 1000)
    }
  }
}
</script>

